    //*******************************111111111111111111111111111111111111111 */
    let data = {
      businessId: this.dataObjj.businessId,
      id: this.dataObjj.id,
      newOrgId: this.dataObjj.newId
    };
    this.axios
      .post(`/risk/org/getOrgCompInfo`, JSON.stringify(data), {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          this.result = response.data;
          this.positons[0] = this.result.evalLevel;
          this.positons[1] = this.result.contrLevel;
        }
      })
      .catch(error => {});
if(this.dataObjj.businessId){
    this.axios
      .post(`/system/remote/getFileList/${this.dataObjj.businessId}`)
      .then(res => {
        if (res.code == "200") {
          let fileList = res.data;
          for (let i = 0; i < fileList.length; i++) {
            this.uploadList.push({
              url: fileList[i].fileId,
              name: fileList[i].filename
            });
          }
        }
      })
      .catch(function(error) {});
      }
    this.axios
      .post("/risk/org/getOrgCompLevelPhone")
      .then(response => {
        if (response.code == "200") {
          let data = response.data;
          let forwCode = data[0].levelCode1;
          let arraTemp = [];
          let lastArraTemp = [];
          lastArraTemp.push({
            levelCode: ["固有风险", "控制措施有效性"],
            color: ""
          });
          arraTemp.push({
            levelCode: data[0].levelCodeName1,
            color: ""
          });
          let flag = true;
          for (let i = 0; i < data.length; i++) {
            let index = 1;
            if (data[i].levelCode1 == forwCode) {
              arraTemp.push(data[i]);
              if (flag)
                lastArraTemp.push({
                  levelCode: data[i].levelCodeName2,
                  color: ""
                });
            } else {
              forwCode = data[i].levelCode1;
              flag = false;
              this.tabledatas2.push(JSON.parse(JSON.stringify(arraTemp)));
              arraTemp = [];
              arraTemp.push({
                levelCode: data[i].levelCodeName1,
                color: ""
              });
              arraTemp.push(data[i]);
            }
          }
          this.tabledatas2.push(JSON.parse(JSON.stringify(arraTemp)));
          this.tabledatas2.push(lastArraTemp);
        }
      })
      .catch(error => {});
    //*******************************111111111111111111111111111111111111111 */
  },
  data() {
    return {
    indicatorName: "",
    //   id: "", //参数
    //   businessId: "", //参数
    //   newId: "", //参数
      //*******************************111111111111111111111111111111111111111 */
      tabledatas2: [],
      uploadList: [],
      result: {},
      positons: ["较低", "中"],
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index, params.row.name);
                    }
                  }
                },
                "下载"
              )
            ]);
          }
        }
      ],
      //*******************************111111111111111111111111111111111111111 */
      // ----------1
      // ----------2
      // tab2开始
      fileList1: [],
      modal1: false,
      activeButtoArr: [],
      activeNames: [],
      activeArr: [],
      activeHeader: [],
      // tab2结束
      // tab3开始
      fileList2: [],
      modal2: false,
      activeButtoArr2: [],
      activeNames2: [],
      activeArr2: [],
      activeHeader2: [],
      // tab3结束
      // ----------2
      title: {},
      topTiTle1: "",
      topTiTle2: "",
      topTiTle3: "",
      topTiTle5:"",
      topTiTle6:"",
      topTiTle7:"",
      topTiTle8:"",
    };
  },
  methods: {
    titleFn() {
      let that = this;
      let data = {
        businessId: this.dataObjj.businessId,
        id: this.dataObjj.id,
        newOrgId: this.dataObjj.newId
      };
      this.axios
        .post("/risk/org/getOrgCompInfo", JSON.stringify(data), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.topTiTle1 = response.data.orgName;
            this.topTiTle2 = `${response.data.evalPeriod}~${response.data.evalPeriod1}`;
            this.topTiTle3 = response.data.evalDate;
            this.topTiTle5 = response.data.evalScore;
            this.topTiTle6 = response.data.evalLevel;
            this.topTiTle7 = response.data.contrScore;
            this.topTiTle8 = response.data.contrLevel;
          }
        })
        .catch(error => {});
    },
    //*******************************111111111111111111111111111111111111111 */
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name){
       if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    //*******************************111111111111111111111111111111111111111 */
    //   --------------2
    // tab2开始
    watchFiled(id) {
      if (id) {
        this.axios
          .post(`/system/remote/getFileList/${id}`)
          .then(res => {
            if (res.code == "200") {
              this.modal1 = true;
              this.fileList = res.data;
            }
          })
          .catch(function(error) {});
      }else{
           this.$Message.error("无文件记录！");
           this.modal1 = false;
      }
    },
    activeButtoArrFun() {
      this.axios
        .post("/risk/org/getDetailByLevelNum", {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.activeButtoArr = response.data;
          }
        })
        .catch(error => {});
    },
    activeButtoFun(a) {
      let that = this;
      this.axios
        .post(`/risk/org/getDetailByLevel/${a}`, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          that.activeNames = response.data;
        })
        .catch(error => {});
    },
    handleChange() {},
    shoufengqin1() {
      let data = {
        businessId: this.dataObjj.businessId,
        id: this.dataObjj.id,
        newOrgId: this.dataObjj.newId
      };
      let that = this;
      data = JSON.stringify(data);
      this.axios
        .post("/risk/org/getOrgRiskInfo", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.activeHeader = response.data.shift().detailVo;
            that.activeArr = response.data;
          }
        })
        .catch(error => {});
    },
    // tab2结束
    // tab2开始
    watchFiled2(id) {
      if (id) {
        this.axios
          .post(`/system/remote/getFileList/${id}`)
          .then(res => {
            if (res.code == "200") {
              this.modal2 = true;
              this.fileList2 = res.data;
            }
          })
          .catch(function(error) {});
      }else{
          this.modal2 = true;
          this.$Message.error("无文件记录！");
      }
    },
    activeButtoArrFun2() {
      this.axios
        .post("risk/org/getContrDetailByLevelNum", {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.activeButtoArr2 = response.data;
          }
        })
        .catch(error => {});
    },
    activeButtoFun2(a) {
      let that = this;
      this.axios
        .post(`/risk/org/getContrDetailByLevel/${a}`, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          that.activeNames2 = response.data;
        })
        .catch(error => {});
    },
    handleChange2() {},
    shoufengqin2() {
      let data = {
        businessId: this.dataObjj.businessId,
        id: this.dataObjj.id,
        newOrgId: this.dataObjj.newId
      };
      let that = this;
      data = JSON.stringify(data);
      this.axios
        .post("/risk/org/getOrgRiskContrInfo ", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.activeHeader2 = response.data.shift().detailVo;
            that.activeArr2 = response.data;
          }
        })
        .catch(error => {});
    }
    // tab2结束
    // ---------------2
  }
};
</script>
