<template>
  <div class="launder-history">
    <div class="topTitle">
      <span class="topTitle-span1">洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机构洗钱风险评估（历史）</span>
    </div>
    <div class="center">
      <div class="table-info">
        <span>评估部门/单位:{{result}}</span>
      </div>
      <div id="main" style="width: 1000px;height:400px;margin: 10px 0;"></div>
      <!-- <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>部门变更历史
      </div>
      <div class="contentCellBody">
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td v-for="(item,index) in tableHeader">{{item.name}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBody">
                <td>{{index+1}}</td>
                <td>{{item.name}}</td>
                <td>{{item.busis}}</td>
                <td>{{item. personer}}</td>
                <td>{{item.startTime}}</td>
                <td>{{item.endTime}}</td>
                <td>{{item.name}}</td>
                <td>{{item.busis}}</td>
              </tr>
            </tbody>
          </table>
        </Row>
      </div>
      <div class="flexCenter pageBox">
        <Page :total="rowCount" :page-size="pageSize" @on-change="pageChange" show-elevator />
      </div> -->
      <Modal :mask-closable="false"  
            v-model="showModal"
            title="机构洗钱风险评估（详情）"
            @on-cancel="cancel"
            class="dialog"
            :styles="{width: '70%'}"
            >
            <component
            :is="tabview" 
            v-if="showModal" 
            :data-objj="dataObjj"
            width="60%"></component>
            <div slot="footer"></div>
        </Modal>
    </div>
  </div>
</template>
<script>
import launderHistoryDetail from './launderHistoryDetail'

export default {
 props: ["dataObj"],
  created(){
     var that = this;
      this.axios
      .post(`/risk/org/getOrgInfoHis/${this.dataObj.id}`)
      .then(response => {
        if (response.code == "200") {
         let sources = response.data;
         let twodem = [[
              "products",
              "固有风险",
              "控制措施有效性",
              "综合评估",
         ]];
         for(let i=0;i<sources.length;i++){
           this.result = sources[i].orgName;
           let source= [
             sources[i].evalDate,
             sources[i].evalScore?sources[i].evalScore:0,
             sources[i].contrScore?sources[i].contrScore:0,
             sources[i].compLevel?this.map[sources[i].compLevel]:0,
             sources[i].id,
             sources[i].newId,
             sources[i].businessId,
             sources[i].evalPeriod,
             sources[i].evalLevel,
             sources[i].contrLevel
           ];
           twodem.push(source);
         }
         this.myChart.setOption({
              dataset:{
                source:twodem
              }
           });
        }
      })
      .catch(error => {});
  },
  data() {
    return {
    result:"",
    params:"",
    dataObjj:{},
    showModal:false,
    tabview:'launderHistoryDetail',
      rowCount: 100,
      pageSize: 10,
      tableHeader: [
        { name: "序" },
        { name: "更新时间" },
        { name: "机构名称" },
        { name: "机构代码" },
        { name: "上级机构名称" },
        { name: "上级机构代码" },
        { name: "跨境标识" },
        { name: "来源系统编码" }
      ],
      tableBody: [
        // {
        //   name: "A股",
        //   busis: "零售-运营管理部2019-12-21",
        //   personer: "zhansan",
        //   startTime: "2019-06-06",
        //   endTime: "2019-08-06",
        //   flag: false
        // }
      ],
      myChart:"",
      map:{
        "A":"10",
        "B":"20",
        "C":"30",
        "D":"40",
        "E":"50"
      },
      option: {
        color: ["#3BA1FF", "#FFA927", "#27A952", "#FFFF00"],
        legend: {
          //  data: ["product",'固有风险评估', '控制措施','综合评估']
        },
        tooltip: {
           trigger:'axis',
           backgroundColor : 'white',
           textStyle : {
            color: 'black',
            decoration: 'none',
            fontFamily: 'Verdana, sans-serif',
            fontSize: 15,
            fontStyle: 'italic',
            fontWeight: 'bold'
          },
           formatter:function(params,ticket,callback){
              var res ="";
              res += '评估期间：'+ params[0].value[7];
              return res;
           }
        },
        dataset: {
          source: []
        },
        xAxis: { type: "category" },
        yAxis: {},
        series: [
          {  
             name:'固有风险评估',
             type: 'bar' ,
              itemStyle:{
                normal:{
                  label:{
                   show:true,
                   position:'top',
                   textStyle:{
                       fontSize:'12'
                     },
                  formatter:function(a){
                    if(a.data[1]==0){
                      return a.data[1];
                    }else{
                    return '{color1|' + a.data[1] + '}/{color2|' + a.data[8] + '}';
                    }
                  },
                  rich: {
                      color1: {
                          color: '#f7ba0e'
                      },
                      color2: {
                          color: '#42a1fe'
                      }
                  },
                  textStyle: {
                    color: '#333'
                  }
                  }
                }                 
              },
         },
          { 
             "name":"控制措施",
             type: 'bar' ,
             itemStyle:{
              normal:{
                label:{
                   show:true,
                   position:'top',
                   textStyle:{
                       fontSize:'12'
                   },
                   formatter:function(a){
                     if(a.data[2]==0){
                       return a.data[2];
                     }else{
                      return '{color1|' + a.data[2] + '}/{color2|' + a.data[9] + '}';
                     }
                  },
                  rich: {
                      color1: {
                          color: '#f7ba0e'
                      },
                      color2: {
                          color: '#42a1fe'
                      }
                  },
                  textStyle: {
                    color: '#333'
                  }
                }
               }
            }
          },
          { 
             "name":"综合评估",
             type: 'bar' ,
              itemStyle:{
               normal:{
                label:{
                   show:true,
                   position:'top',
                   textStyle:{
                       fontSize:'12'
                   },
                   formatter:function(a){
                       if(a.data[3]=="10"){
                            return "A";
                       }
                       if(a.data[3]=="20"){
                           return "B";
                       }
                       if(a.data[3]=="30"){
                           return "C";
                       }
                       if(a.data[3]=="40"){
                           return "D";
                       }
                       if(a.data[3]=="50"){
                           return "E";
                       }
                   }
                }
               }
            }
          }
        ]
      }
    };
  },
  components:{
        launderHistoryDetail
    },
  mounted() {
    this.myChart = this.$echarts.init(document.getElementById("main"));
    this.myChart.setOption(this.option);
    this.myChart.on("click", (params)=> {
      this.dataObjj={
         id : params.data[4],
         newId : params.data[5],
         businessId : params.data[6]
      };
      this.showModal = true;
      var name = parseInt(params.name);
    });
  },
  methods: {
    cancel(){},

    // pageChange(pageNo) {
    //   let that = this;
    //   let params = new FormData();
    //   params.append("code", "list_app_fina");
    //   params.append("pageNo", pageNo);
    // }
  }
};
</script>
<style lang="scss" @scoped>
.launder-history {
  overflow: hidden;
  .center {
    background: #fff;
    padding: 20px 20px 40px;
    margin-top: 10px;
    .table-info {
      font-size: 14px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .tableBox {
      margin-top: 0px;
    }
  }
}
</style>


